import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AddNewComponent } from '../add-new/add-new.component';
import { FormControl, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { Big } from 'big.js';
import { ServiceService } from 'src/app/service/service.service';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  date = new Date();
  displayedColumns =
    ['position', 'name', 'weight', 'symbol'];
  page = 1;
  pageSize = 10;
  dataSource: any[] = [];
  form: FormGroup;
  numberDate: string;
  listCustomer: any[] = [];
  customerId: string;
  constructor(private modalService: NgbModal,
    private router: Router,
    private service: ServiceService,
    private SpinnerService: NgxSpinnerService) { }

  async ngOnInit() {
    this.SpinnerService.show();
    this.initialFrom();
    await this.myFunction();
  
    this.date = new Date();
    this.get();
    this.getCustomer();
    
    setTimeout(() => {
      this.SpinnerService.hide();
  }, 1000);
  }

  getCustomer() {
    this.listCustomer = [];
    this.service.customer().subscribe(resp => {
      if (resp && resp.length !== 0) {
        this.listCustomer = resp;
      }
    },
      (errorRes) => {
        if (errorRes.error && errorRes.error.errorDesc) {
          return;
        }
      });
  }

  async myFunction() {
    this.numberDate = '0';
    this.dataSource.forEach(item => {
      this.numberDate = this.localeString(parseInt(this.formatNumber(this.numberDate)) + parseInt(this.formatNumber(item.sumAmt)));
    })
  }

  onClickDetailDialog() {
    const modalRef = this.modalService.open(AddNewComponent, {
      windowClass: 'modal-fit-content', backdrop: 'static', keyboard: false
    });
    let page = 'Add';
    this.setModalRef(modalRef, { page });
    return modalRef;
  }

  setModalRef(modalRef: any, modalInfo: any) {
    for (const item of Object.keys(modalInfo)) {
      modalRef.componentInstance[item] = modalInfo[item];
    }
    return modalRef;
  }


  detail(item: any) {
    let data = {
      customerId: item.customerId,
      dueDate: item.dueDate,
      status: 'serach'
    }
    localStorage.removeItem('dataMain');
    localStorage.setItem('dataMain',JSON.stringify(data));
    this.router.navigate(['/view-detail'])
  }

  initialFrom() {
    this.form = new FormGroup({
      date: new FormControl(''),
    });

    this.form.controls.date.patchValue(this.formatDateInput(new Date()));
  }

  formatDateInput(date: Date) {
    return moment(date).format('YYYY-MM-DD');
  }

  formatDateTimeInput(date: Date) {
    return moment(date).format('DD-MM-YYYY HH:mm:ss');
  }

  formatDateInput2(date: Date) {
    return moment(date, 'YYYY-MM-DD').format('DD-MM-YYYY');
  }

  localeString(data: number) {
    let points = data.toLocaleString('en-US');
    return points;
  }

  formatNumber(value: string) {
    if (value && value !== '') {
      let parts = String(value).replace(/[^-\d.]/g, '').split('.');
      parts[0] = new Big(parts[0].padStart(1, '0')).toString();
      return parts[0];
    }
  }

  saveAsTextFile() {
    let fileText = '';
    fileText += 'วันที่: ' + this.isoStringToDate(this.formatDateInput(this.form.controls.date.value)) + "<BR><BR>";
    this.dataSource.forEach((element, i) => {
      fileText += (i + 1) + '.' + element.customerName + ' = ' + element.sumAmt + "<BR>"
    });
    fileText += 'รวม ' + this.numberDate;

    let w = null;
    setTimeout(() => {
      w = window.open("", "_blank");
      w.document.write(fileText);
    });
  }

  isoStringToDate(data: string) {
    var date = new Date(data);
    return date.toLocaleDateString('th-TH', {
      weekday: "long",
      day: 'numeric',
      month: 'long',
      year: 'numeric',
    })
  }

  get() {
    this.dataSource= [];
    this.numberDate = '0';
    this.service.installments(this.formatDateInput2(this.form.controls.date.value)).subscribe(resp => {
      if (resp && resp.length !== 0) {
        resp.forEach(item => {
          this.dataSource.push({
            customerId: item.customerId,
            customerName: item.customerName,
            dueDate: item.dueDate,
            sumAmt: this.localeString(item.sumAmt)
          })
        })
        this.dataSource.forEach(item => {
          this.numberDate = this.localeString(parseInt(this.formatNumber(this.numberDate)) + parseInt(this.formatNumber(item.sumAmt)));
        })
      }
    },
      (errorRes) => {
        if (errorRes.error && errorRes.error.errorDesc) {
          return;
        }
      });
  }

  search() {
    let data = {
      customerId: this.customerId,
      dueDate: null,
      status: 'cus'
    }
    localStorage.removeItem('dataMain');
    localStorage.setItem('dataMain',JSON.stringify(data));
    this.router.navigate(['/view-detail'])
  }
  
}

