import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { ServiceService } from 'src/app/service/service.service';

@Component({
  selector: 'app-account-list',
  templateUrl: './account-list.component.html',
  styleUrls: ['./account-list.component.css']
})
export class AccountListComponent implements OnInit {
  form: FormGroup;
  listData: any[] = [];
  page = 1;
  pageSize = 10;
  sumNumber: String = '0';
  constructor(private service: ServiceService,
    private router: Router) { }

  ngOnInit() {
    this.initialFrom();
    this.search();
  }

  initialFrom() {
    this.form = new FormGroup({
      startDate: new FormControl(''),
      endDate: new FormControl(''),
    });

    var date = new Date();
    date.setDate(date.getDate() - 30);
    this.form.controls.startDate.patchValue(this.formatDateInput(date));
    this.form.controls.endDate.patchValue(this.formatDateInput(new Date()));
  }

  formatDateInput(date: Date) {
    return moment(date).format('YYYY-MM-DD');
  }

  formatDateInput2(date: Date) {
    return moment(date, 'YYYY-MM-DD').format('DD-MM-YYYY');
  }

  search() {
    this.listData = [];
    let sum = 0;
    this.service.search(this.formatDateInput2(this.form.controls.startDate.value), this.formatDateInput2(this.form.controls.endDate.value)).subscribe(resp => {
      if (resp && resp.length !== 0) {
        resp.forEach(item => {
          this.listData.push({
            customerId: item.customerId,
            customerName: item.customerName,
            createDate: item.createDate,
            sumIncome: this.localeString(item.sumIncome)
          });
          sum += item.sumIncome;
        });
        this.sumNumber = this.localeString(sum);
      }
    })
  }

  searchDetail(item) {
    let data = {
      customerId: item.customerId,
      dueDate: item.createDate,
      status: 'account'
    }
    localStorage.removeItem('dataMain');
    localStorage.setItem('dataMain', JSON.stringify(data));
    this.router.navigate(['/view-detail'])
  }

  localeString(data: number) {
    let points = data.toLocaleString('en-US');
    return points;
  }



}
