import { Component, OnInit } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ViewInstallmentComponent } from '../view-installment/view-installment.component';
import { PopupComfirmComponent } from '../popup-comfirm/popup-comfirm.component';
import * as moment from 'moment';
import { Router } from '@angular/router';
import { AddNewComponent } from '../add-new/add-new.component';
import { ServiceService } from 'src/app/service/service.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { async } from '@angular/core/testing';

@Component({
  selector: 'app-view-detail',
  templateUrl: './view-detail.component.html',
  styleUrls: ['./view-detail.component.css']
})
export class ViewDetailComponent implements OnInit {
  dataSource: any = [];
  listData: any;
  dataMain: any;
  listDataTran: any[] = [];
  installmentTemp: any;

  date: string;
  listNumber: any = [
    {
      id: 1,
      name: 'จ่ายเฉพาะดอก'
    }, {
      id: 2,
      name: 'ลดต้นลดดอก'
    }
  ];
  constructor(private modalService: NgbModal,
    private router: Router,
    private service: ServiceService,
    private SpinnerService: NgxSpinnerService) { }

  async ngOnInit() {
    this.SpinnerService.show();
    this.dataMain = JSON.parse(localStorage.getItem('dataMain'));
    this.date = this.isoStringToDate(this.formatDateInput(this.dataMain.dueDate));
    this.listApi();

    if (this.dataMain.status === 'cus') {
      await this.getCustomer();
    } else if (this.dataMain.status === 'account') {
      await this.searchCus()
    } else {
      await this.getData()
    }
    setTimeout(() => {
      this.SpinnerService.hide();
    }, 1000);
  }

  async getCustomer() {
    this.listDataTran = [];
    this.SpinnerService.show();

    this.service.getCustomer(this.dataMain.customerId).subscribe(async resp => {
      if (resp && resp.length !== 0) {
        resp.forEach(async item => {
          await this.setData(item);
        });
      }
      setTimeout(() => {
        this.SpinnerService.hide();
      }, 10000);
    },
      (errorRes) => {
        if (errorRes.error && errorRes.error.errorDesc) {
          return;
        }
      });
  }

  async searchCus() {
    this.listDataTran = [];
    this.SpinnerService.show();

    this.service.searchCus(this.dataMain.customerId, this.formatDateInput1(this.dataMain.dueDate), this.formatDateInput1(this.dataMain.dueDate)).subscribe(async resp => {
      if (resp && resp.length !== 0) {
        resp.forEach(async item => {
          await this.setData(item);
        });
      }
      setTimeout(() => {
        this.SpinnerService.hide();
      }, 10000);
    },
      (errorRes) => {
        if (errorRes.error && errorRes.error.errorDesc) {
          return;
        }
      });
  }

  async getData() {
    this.listDataTran = [];
    this.SpinnerService.show();
    this.service.getViewDetail(this.dataMain.customerId, this.formatDateInput1(this.dataMain.dueDate)).subscribe(async resp => {
      if (resp && resp.length !== 0) {
        resp.forEach(async item => {
          await this.setData(item);
        });
      }
      setTimeout(() => {
        this.SpinnerService.hide();
      }, 10000);
    },
      (errorRes) => {
        if (errorRes.error && errorRes.error.errorDesc) {
          return;
        }
      });
  }

  async setData(item){
    this.listDataTran.push({
      transactionId: item.transactionId,
      description: item.description,
      createDate: this.isoStringToDate(item.createDate),
      customerName: item.customerName,
      transactionType: item.transactionType,
      transactionTypeDesc: item.transactionType ? this.listData.transactionType.find(event => event.transactionTypeId === item.transactionType).transactionTypeDesc : '',
      interestType: item.interestType,
      interestTypeName: item.interestType ? this.listNumber.find(event => event.id === item.interestType).name : '',
      interestRate: item.interestRate,
      paymentType: item.paymentType,
      contractFirstDueDate: this.isoStringToDate(item.contractFirstDueDate),
      principle: this.localeString(item.principle),
      total: this.localeString(item.total),
      contractPeriod: item.contractPeriod,
      firstDownAmt: this.localeString(item.firstDownAmt),
      remaining: this.localeString(item.remaining),
      installmentAmt: this.localeString(item.installmentAmt),
      income: this.localeString(item.income),
      paymentDateSpecific: item.paymentDateSpecific,
      daily: item.daily,
      paymentDate: item.paymentDate,
    });
  }

  formatDateInput(date: string) {
    return moment(date, 'DD/MM/YYYY').format('YYYY-MM-DD');
  }

  formatDateInput1(date: string) {
    return moment(date, 'DD/MM/YYYY').format('DD-MM-YYYY');
  }

  isoStringToDate(data: string) {
    var date = new Date(data);
    return date.toLocaleDateString('th-TH', {
      // weekday: 'long',
      day: 'numeric',
      month: 'long',
      year: 'numeric',
    })
  }

  async viewInstallmentl(item: any) {
    this.installmentTemp = [];
    return new Promise<any>((resolve, reject) => {
      this.service.viewInstallmentl(item.transactionId).subscribe(resp => {
        if (resp && resp.length !== 0) {
          resp.forEach((item, i) => {
            this.installmentTemp.push({
              'installmentNo': i + 1,
              'dueDate': item.dueDate,
              'installmentAmt': this.localeString(item.installmentAmt),
              'installmentId': item.installmentId,
              'isPrepaid': item.isPrepaid
            });
          });
          resolve(true);
        }
      },
        (errorRes) => {
          if (errorRes.error && errorRes.error.errorDesc) {
            return;
          }
        });
    });
  }

  async onClickViewInstallmentlDialog(item: any) {
    await this.viewInstallmentl(item);
    const modalRef = this.modalService.open(ViewInstallmentComponent, {
      windowClass: 'modal-fit-content-view', backdrop: 'static', keyboard: false
    });
    modalRef.componentInstance.isCheckStatus = 'edit';
    modalRef.componentInstance.installment = this.installmentTemp;
    modalRef.componentInstance.transactionId = item.transactionId;
    return modalRef;
  }

  async delete(item: any) {
    const cf = await this.onClickDialog('ลบ');
    if (cf) {
      this.SpinnerService.show();
      return new Promise<any>((resolve, reject) => {
        this.service.delete(item.transactionId).subscribe(resp => {
          if (resp && resp.length !== 0) {
            if (this.dataMain.status === 'cus') {
              this.getCustomer();
            } else if (this.dataMain.status === 'account') {
              this.searchCus()
            } else {
              this.getData()
            }
            setTimeout(() => {
              this.SpinnerService.hide();
            }, 1000);
            resolve(true);
          }
        },
          (errorRes) => {
            if (errorRes.error && errorRes.error.errorDesc) {
              return;
            }
          });
      });
    }
  }

  async onClickDialog(message: string) {
    const modalRef = this.modalService.open(PopupComfirmComponent, {
      backdrop: 'static', keyboard: false
    });
    // let message = 'ลบ';
    let flag = true;
    this.setModalRef(modalRef, { message, flag });
    return modalRef.result;
  }

  setModalRef(modalRef: any, modalInfo: any) {
    for (const item of Object.keys(modalInfo)) {
      modalRef.componentInstance[item] = modalInfo[item];
    }
    return modalRef;
  }


  async saveAsTextFile(item: any) {
    await this.viewInstallmentl(item);
    let fileText = '';
    fileText += 'รายการบัญชี <BR> ------------ <BR>';
    fileText += 'ชื่อลูกค้า: ' + item.customerName + '<BR>';
    fileText += 'วันที่: ' + item.createDate + '<BR>';
    fileText += 'รายการ: ' + item.description + '<BR>';
    fileText += 'เงินต้น: ' + this.localeString(item.principle) + '<BR>';
    fileText += 'ประเภท: ' + this.listData.transactionType.find(event => event.transactionTypeId === item.transactionType).transactionTypeDesc + '<BR>';
    fileText += 'อัตราดอกเบี้ย: ' + item.interestRate + '%<BR>';
    fileText += 'ยอดรวม: ' + this.localeString(item.total) + '<BR>';
    fileText += 'ดาวน์/หักดอก: ' + item.firstDownAmt + '<BR>';
    fileText += 'คงเหลือผ่อน: ' + this.localeString(item.remaining) + '<BR>';
    fileText += 'จ่ายแบบ: ' + this.listData.paymentType.find(event => event.paymentTypeId === item.paymentType).paymentTypeDesc + '<BR>';
    fileText += 'จำนวนงวด: ' + item.contractPeriod + '<BR>';
    fileText += 'ผ่อนงวดละ: ' + this.localeString(item.installmentAmt) + '<BR>';
    fileText += 'วันที่เริ่มผ่อน: ' + item.contractFirstDueDate + '<BR>';
    fileText += '------------ <BR> รายการจ่ายค่างวด <BR> ------------ <BR>';
    this.installmentTemp.forEach((item, i) => {
      fileText += '(' + (i + 1) + ') ' + this.isoStringToDate(this.formatDateInput(item.dueDate)) + ' = ' + this.localeString(item.installmentAmt) + '<BR>'
    })
    let w = null;
    setTimeout(() => {
      w = window.open("", "_blank");
      w.document.write(fileText);
    });
  }

  localeString(data: number) {
    let points = data.toLocaleString('en-US');
    return points;
  }

  formatDateTimeInput(date: Date) {
    return moment(date).format('DD-MM-YYYY HH:mm:ss');
  }

  gotoBack() {
    if (this.dataMain.status === 'cus') {
      this.router.navigate(['/search-customer'])
    } else if (this.dataMain.status === 'account') {
      this.router.navigate(['/account-list']);
    } else {
      this.router.navigate(['/main']);
    }
  }

  listApi() {
    this.listData = {
      "interestType": [
        {
          "interestTypeId": 1,
          "interestTypeDesc": "รายปี",
          "seqNo": 1
        },
        {
          "interestTypeId": 2,
          "interestTypeDesc": "รายเดือน",
          "seqNo": 2
        }
      ],
      "paymentType": [
        {
          "paymentTypeId": 1,
          "paymentTypeDesc": "รายวัน",
          "seqNo": 1
        },
        {
          "paymentTypeId": 2,
          "paymentTypeDesc": "รายเดือน",
          "seqNo": 2
        },
        {
          "paymentTypeId": 3,
          "paymentTypeDesc": "ระบุวัน",
          "seqNo": 3
        }
      ],
      "transactionType": [
        {
          "transactionTypeId": 1,
          "transactionTypeDesc": "ทอง",
          "seqNo": 1,
          "coler": '#ec8b1c'
        },
        {
          "transactionTypeId": 2,
          "transactionTypeDesc": "ทุน",
          "seqNo": 2,
          "coler": '#ec8b1c'
        },
        {
          "transactionTypeId": 3,
          "transactionTypeDesc": "ออม",
          "seqNo": 3,
          "coler": '#3d5ff5'
        },
        {
          "transactionTypeId": 4,
          "transactionTypeDesc": "ความงาม",
          "seqNo": 4,
          "coler": '#ec8b1c'
        },
        {
          "transactionTypeId": 5,
          "transactionTypeDesc": "เบอร์",
          "seqNo": 5,
          "coler": '#ec8b1c'
        },
        {
          "transactionTypeId": 6,
          "transactionTypeDesc": "เพชร",
          "seqNo": 6,
          "coler": '#ec8b1c'
        },
        {
          "transactionTypeId": 7,
          "transactionTypeDesc": "กู้",
          "seqNo": 7,
          "coler": '#ec8b1c'
        }
      ]
    }

    // this.radioTypeList = this.listData.transactionType;
  }

  onClickDetailDialog(item: any) {
    const modalRef = this.modalService.open(AddNewComponent, {
      windowClass: 'modal-fit-content', backdrop: 'static', keyboard: false
    });
    let page = 'Edit';
    this.setModalRef(modalRef, { page, item });
    return modalRef;
  }

  async close(item: any) {
    const cf = await this.onClickDialog('ยืนยันปิดบัญชี');
    if (cf) {
      this.SpinnerService.show();
      return new Promise<any>((resolve, reject) => {
        this.service.close(item.transactionId).subscribe(resp => {
          if (resp && resp.length !== 0) {
            if (this.dataMain.status === 'cus') {
              this.getCustomer();
            } else if (this.dataMain.status === 'account') {
              this.searchCus()
            } else {
              this.getData()
            }
            setTimeout(() => {
              this.SpinnerService.hide();
            }, 1000);
            resolve(true);
          }
        },
          (errorRes) => {
            if (errorRes.error && errorRes.error.errorDesc) {
              return;
            }
          });
      });
    }
  }
}
