import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { ServiceService } from 'src/app/service/service.service';
import * as moment from 'moment';
import { Big } from 'big.js';
import { Observable } from 'rxjs';
import { FormControl } from '@angular/forms';
import { map, startWith } from 'rxjs/operators';
import { async } from '@angular/core/testing';

@Component({
  selector: 'app-search-customer',
  templateUrl: './search-customer.component.html',
  styleUrls: ['./search-customer.component.css']
})
export class SearchCustomerComponent implements OnInit {
  listCustomer: any[] = [];
  // customerId: number;
  listDataTran: any[] = [];
  installmentTemp: any[] = [];

  sumDelete: string;
  sumAdd: string;
  sumAmt: String = '0';

  customerId = new FormControl('');
  options: string[] = ['One', 'Two', 'Three'];
  filteredOptions: Observable<string[]>;
  constructor(private service: ServiceService,
    private router: Router,
    private SpinnerService: NgxSpinnerService) { }

  ngOnInit() {
    this.sumDelete = '0';
    this.sumAdd = '0';
    this.getCustomer();

  }

  _filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.listCustomer.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  getCustomer() {
    this.service.customer().subscribe(resp => {
      if (resp && resp.length !== 0) {
        this.listCustomer = resp;
        this.filteredOptions = this.customerId.valueChanges.pipe(
          startWith(''),
          map(value => this._filter(value || '')),
        );
      }
    },
      (errorRes) => {
        if (errorRes.error && errorRes.error.errorDesc) {
          return;
        }
      });
  }

  getListCustomer() {
    this.service.getListCustomer().subscribe(resp => {
      if (resp && resp.length !== 0) {
        this.listCustomer = resp;
      }
    },
      (errorRes) => {
        if (errorRes.error && errorRes.error.errorDesc) {
          return;
        }
      });
  }

  search() {
    let customerId = this.customerId.value ? this.listCustomer.find(item => item.name === this.customerId.value).customerId : null;
    let data = {
      customerId: customerId,
      dueDate: null,
      status: 'cus'
    }
    localStorage.removeItem('dataMain');
    localStorage.setItem('dataMain', JSON.stringify(data));
    this.router.navigate(['/view-detail'])
  }

  async getCustomerList() {
    let customerId = this.customerId.value ? this.listCustomer.find(item => item.name === this.customerId.value).customerId : null;
    this.listDataTran = [];
    this.sumDelete = '0';
    this.sumAdd = '0';
    this.SpinnerService.show();
    return new Promise<any>((resolve, reject) => {

      this.service.getCustomer(customerId).subscribe(async resp => {

        if (resp && resp.length !== 0) {
          this.listDataTran = resp;
          this.listDataTran.forEach(async element => {
            if (!element.isClosed) {
              await this.viewInstallmentl(element.transactionId);
              await this.sum(element.transactionType);
              if (this.sumAdd && this.sumDelete) {
                this.sumAmt = this.localeString(parseInt(this.formatNumber(this.sumAdd)) - parseInt(this.formatNumber(this.sumDelete)));
              }
            }
          });
          resolve(true);
        }
        setTimeout(() => {
          this.SpinnerService.hide();
        }, 1000);
      },
        (errorRes) => {
          if (errorRes.error && errorRes.error.errorDesc) {
            return;
          }
        });
    });
  }

  async viewInstallmentl(transactionId) {
    return new Promise<any>((resolve, reject) => {
      this.service.viewInstallmentl(transactionId).subscribe(async resp => {
        if (resp && resp.length !== 0) {
          this.installmentTemp = [];
          resp.forEach((item, i) => {
            this.installmentTemp.push({
              'installmentNo': i + 1,
              'dueDate': item.dueDate,
              'installmentAmt': this.localeString(item.installmentAmt),
              'isPrepaid': item.isPrepaid
            });
          });
          resolve(true);
        }
      },
        (errorRes) => {
          if (errorRes.error && errorRes.error.errorDesc) {
            return;
          }
        });
    });
  }

  localeString(data: number) {
    let points = data.toLocaleString('ban');
    return points;
  }

  async sum(transactionType) {
    // ลบ
    let installmentDelete = 0;
    let installmentAdd = 0;
    if (transactionType === 1 || transactionType === 2 || transactionType === 4 || transactionType === 7) {
      this.installmentTemp.forEach(item => {
        if (!item.isPrepaid) {
          const todayDate = moment(new Date(), 'YYYY-MM-DD');
          var arr1 = (todayDate.year() + '-' + (todayDate.month() + 1) + '-' + todayDate.date()).toString().split(/[-]/);
          let newDate = new Date(Number(arr1[0]), Number(arr1[1]) - 1, Number(arr1[2]));

          const date = moment(this.formatDateInput(item.dueDate), 'YYYY-MM-DD');
          var arr2 = (date.year() + '-' + (date.month() + 1) + '-' + date.date()).toString().split(/[-]/);
          let dueDate = new Date(Number(arr2[0]), Number(arr2[1]) - 1, Number(arr2[2]));
          if (newDate <= dueDate) {
            installmentDelete += parseInt(this.formatNumber(item.installmentAmt));
          }
        }
      });
      this.sumDelete = this.localeString(parseInt(this.formatNumber(this.sumDelete)) + installmentDelete);
    } else {
      this.installmentTemp.forEach(item => {
        const todayDate = moment(new Date(), 'YYYY-MM-DD');
        var arr3 = (todayDate.year() + '-' + (todayDate.month() + 1) + '-' + todayDate.date()).toString().split(/[-]/);
        let newDate = new Date(Number(arr3[0]), Number(arr3[1]) - 1, Number(arr3[2]));

        const date = moment(this.formatDateInput(item.dueDate), 'YYYY-MM-DD');
        var arr4 = (date.year() + '-' + (date.month() + 1) + '-' + date.date()).toString().split(/[-]/);
        let dueDate = new Date(Number(arr4[0]), Number(arr4[1]) - 1, Number(arr4[2]));
        if (newDate >= dueDate) {
          installmentAdd += parseInt(this.formatNumber(item.installmentAmt));
        }
        if (item.isPrepaid) {
          installmentAdd += parseInt(this.formatNumber(item.installmentAmt));
        }
      });
      this.sumAdd = this.localeString(parseInt(this.formatNumber(this.sumAdd)) + installmentAdd);
    }

  }

  formatDateInput(date: Date) {
    return moment(date, 'DD/MM/YYYY').format('YYYY-MM-DD');
  }


  formatNumber(value: string) {
    if (value && value !== '') {
      let parts = String(value).replace(/[^-\d.]/g, '').split('.');
      parts[0] = new Big(parts[0].padStart(1, '0')).toString();
      return parts[0];
    }
  }

}
